import React from 'react'
import Button from '../../../components/Button'
import styled from "styled-components";
import {useTranslation} from "react-i18next";

interface EquipItemProps {
    card: any
    onClick: () => void
}

const EquipItem: React.FC<EquipItemProps> = ({card, onClick}) => {
    const {t} = useTranslation()

    return (
        <div className={'item ' + (card.removing ? 'removing' : '')}>
            {card && !card.key ? <img src={card.imgUrl} alt="" onClick={onClick}/> :
                <BorderImg onClick={onClick}/>
            }
            {card && !card.key ?
                <Button text={card.removing ? t('common.Loading') : t('common.Remove')} size="sm" bgColor="orange"
                        onClick={onClick}
                        disabled={card.disabled || card.removing}/> :
                <Button text={t('common.Equip')} size="sm" bgColor="blue" onClick={onClick}/>
            }
        </div>
    )
}

const BorderImg = styled.div`
  width: 180px;
  height: 210px;
  object-fit: contain;
  box-sizing: border-box;
  border: 8px dashed #ffa115;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border-radius: 4px;

  &:hover {
    border-color: #c25a3e;
  }

  &:after {
    content: '+';
    font-weight: 800;
    font-size: 30px;
    color: #ffa115;
  }
`;
export default EquipItem
