import Web3 from 'web3'
import {Contract} from 'web3-eth-contract'
import {AbiItem} from 'web3-utils'
import ERC20ABI from '../sushi/lib/abi/erc20.json'

export const getContract = (provider: any, address: string) => {
    const web3 = new Web3(provider)
    return new web3.eth.Contract(
        (ERC20ABI as unknown) as AbiItem,
        address,
    )
}

export const getAllowance = async (
    lpContract: Contract,
    masterChefContract: Contract,
    account: string,
): Promise<string> => {
    try {
        return await lpContract.methods
            .allowance(account, masterChefContract.options.address)
            .call()
    } catch (e) {
        return '0'
    }
}

export const getBalance = async (
    provider: any,
    tokenAddress: string,
    userAddress: string,
): Promise<string> => {
    const lpContract = getContract(provider, tokenAddress)
    try {
        return await lpContract.methods
            .balanceOf(userAddress)
            .call()
    } catch (e) {
        return '0'
    }
}

export const balanceOf = async (
    lgdTokenContract: any,
    account: string
): Promise<string> => {
    try {
        return await lgdTokenContract.methods
            .balanceOf(account)
            .call()
    } catch (e) {
        return '0'
    }
}
