import React, {useCallback, useEffect} from 'react'
import Button from '../../../components/Button'
import './SearchCard.css'
import FilterCard from './FilterCard'
import {Modal} from 'react-bootstrap'
import {Contract} from "web3-eth-contract";
import {useWallet} from "use-wallet";
import {heroArmory, lgdHero, shop} from "../../../sushi/utils";
import {CardQuality} from "../../../utils/card-quality";
import {getAvatarUrl, useFetchState, useLgdBalance} from "../../../utils";
import {useTranslation} from "react-i18next";
import CardDesc from "../../../components/CardDesc";

interface SearchCardProps {
    heroContract: Contract
    shopContract: Contract
    lgdHeroContract: Contract
    web3: any
}

const defaultCard: any = {
    name: '',
    quality: '',
    fightRate: '',
    cardShowRate: '',
    fight: '',
    prop: '',
    tokenId: ''
}
const SearchCard: React.FC<SearchCardProps> = ({shopContract, heroContract, lgdHeroContract, web3}) => {
    const {account} = useWallet()
    const {t} = useTranslation()
    const {lgdBalance} = useLgdBalance()
    const [fullRows, setFullRows] = useFetchState([])
    const [rows, setRows] = useFetchState([])
    const [card, setCard] = useFetchState(defaultCard)
    const [tokenId, setTokenId] = useFetchState('')
    const [searchValue, setSearchValue] = useFetchState('')
    const [renameAmountNoEnough, setRenameAmountNoEnough] = useFetchState(false)
    const [renameAmount, setRenameAmount] = useFetchState('')
    const [currentCard, setCurrentCard] = useFetchState('')
    const [nameBtnLoading, setNameBtnLoading] = useFetchState(false)
    const [showRenameFunc, setShowRenameFunc] = useFetchState(false)
    const [showTransferFunc, setShowTransferFunc] = useFetchState(false)
    const [renameValue, setRenameValue] = useFetchState('');
    const [isRenameSuccess, setIsRenameSuccess] = useFetchState(false);
    const [isRenameError, setIsRenameError] = useFetchState(false)
    const [renameErrorMsg, setRenameErrorMsg] = useFetchState(t('cards.msg1'));
    const [isLoading, setIsLoading] = useFetchState(false);

    const [transferValue, settransferValue] = useFetchState('');
    const [isTranferSuccess, setIsTranferSuccess] = useFetchState(false);
    const [isTranferError, setIsTranferError] = useFetchState(false)
    const [tranferErrorMsg, setTranferErrorMsg] = useFetchState(t('cards.msg2'));

    const closeRename = () => setShowRenameFunc(false)
    const closeTransfer = () => setShowTransferFunc(false)
    const showTransfer = () => setShowTransferFunc(true)
    const handleShowRename = useCallback(async (id: string) => {
        setCurrentCard(id)
        if (!renameAmount) {
            setNameBtnLoading(true)
            let amount = await shop.nameAmount(shopContract, account)
            amount = web3.utils.fromWei(amount) * 1
            setRenameAmountNoEnough(lgdBalance * 1 < amount)
            setRenameAmount(amount)
            setNameBtnLoading(false)
        }
        let card = rows.find((item: any) => item.tokenId === id) || {}
        setTokenId(id)
        setCard(card)
        setRenameValue(card.name)
        setIsRenameSuccess(false);
        setShowRenameFunc(true);
    }, [web3.utils, shopContract, account, rows, setTokenId, setCard, setIsRenameSuccess, setRenameValue, setShowRenameFunc, renameAmount, setCurrentCard, setNameBtnLoading, setRenameAmount, setRenameAmountNoEnough, lgdBalance])

    function handleShowTransfer(id: string) {
        let card = rows.find((item: any) => item.tokenId === id) || {}
        setTokenId(id)
        setCard(card)
        settransferValue('');
        setIsTranferSuccess(false);
        showTransfer();
    }

    function onKeydownSearch(e: any) {
        if (e.keyCode === 13) {
            search()
        }
    }

    function search() {
        let list = [...fullRows].filter((item: any) => {
            if (searchValue === '') {
                return true
            }
            try {
                return new RegExp(searchValue, 'i').test(item.name)
            } catch (e) {
                return item.name.includes(searchValue)
            }
        })
        setRows(list)
    }

    const handleChangeName = (e: any) => {
        const value = e.currentTarget.value;
        if (value === 'iron') {
            setIsRenameError(true);
            setRenameErrorMsg(t('cards.msg1'))
        } else if (value.length >= 12) {
            setIsRenameError(true);
            setRenameErrorMsg(t('cards.msg4'));
        } else {
            setIsRenameError(false);
        }
        setRenameValue(value);
    }

    const handleChangeAddress = (e: any) => {
        const value = e.currentTarget.value;
        if (!isValidReceivingAddress(value)) {
            setIsTranferError(true);
        } else {
            setIsTranferError(false);
        }
        settransferValue(value);
    }

    const isValidReceivingAddress = (address: any) => {
        if (!address || !/^(0x)?[0-9a-fA-F]{40}$/.test(address)) {
            return false;
        }
        return true;
    }

    const handleSubmitRename = () => {
        setIsLoading(true);
        shop.changeHeroName(shopContract, tokenId, renameValue, account).then(res => {
            let newCard = res.events.ChangeName.returnValues
            setIsLoading(false);
            setIsRenameSuccess(true)
            if (tokenId === newCard.tokenId) {
                setCard({
                    ...card,
                    name: newCard.name
                })
            }

            let newList = rows.map((item: any) => {
                if (item.tokenId === newCard.tokenId) {
                    item.name = newCard.name
                }
                return item
            })
            setRows(newList)
            setFullRows(newList)
            setTimeout(() => {
                closeRename()
            }, 2000)
        }).catch(() => {
            setIsLoading(false);
            setIsRenameError(false)
        })
    }

    const handleSubmitReceiver = () => {
        setIsLoading(true);
        lgdHero.safeTransferFrom(lgdHeroContract, transferValue, tokenId, account).then(res => {
            setIsLoading(false);
            setIsTranferSuccess(true)
            setTranferErrorMsg(t('cards.msg3'))
            let newList = [...rows]
            let index = newList.findIndex(item => item.tokenId === tokenId)
            if (index > -1) {
                newList.splice(index, 1)
            }
            setCard(defaultCard)
            setTokenId('')
            setRows(newList)
            setFullRows(newList)
            setTimeout(() => {
                closeTransfer()
            }, 1000)
        }).catch((e) => {
            setIsLoading(false);
            setIsTranferError(true)
            setTranferErrorMsg(e?.message)
        })
    }

    useEffect(() => {
        if (heroContract && rows.length === 0) {
            heroArmory.getHeros(heroContract, account).then(res => {
                const all = res.map((item: any) => {
                    let q = CardQuality[item.quality]
                    return {
                        name: item.name,
                        quality: q.name,
                        fightRate: q.fight,
                        cardShowRate: q.rate,
                        fight: item.fight,
                        prop: item.prop,
                        tokenId: item.tokenId,
                        heroTypeId: item.heroTypeId,
                        avatarName: `quality_heroTypeId_prop_${item.quality}_${item.heroTypeId}_${item.prop}`,
                        imgUrl: getAvatarUrl(item),
                    }
                })
                console.log('mycard', all)
                setRows(all)
                setFullRows(all)
            })
        }
    }, [heroContract, web3, account, rows.length, setFullRows, setRows])

    return (
        <div className="search__card">
            <div className="search__card--container">
                <FilterCard
                    onChange={(e: any) => {
                        setSearchValue(e.target.value)
                    }}
                    onKeyDown={(e: any) => {
                        onKeydownSearch(e)
                    }}
                    onClick={search}
                    value={searchValue}
                />
                <p className="result">{t('cards.FoundNumCards', {num: rows.length})}</p>
                <div className="list-card-result">
                    {
                        rows.map((card: any, index: number) => <div className="card-result" key={index}>
                            <img src={card.imgUrl} alt=""/>
                            <div className="infor-card">
                                <CardDesc card={card}/>
                                <div className="rename">
                                    <Button
                                        text={nameBtnLoading && currentCard === card.tokenId ? t('common.Loading') : t('cards.Rename')}
                                        size="sm"
                                        disabled={nameBtnLoading && currentCard === card.tokenId}
                                        onClick={() => handleShowRename(card.tokenId)}/>
                                </div>
                                <div className="transfer">
                                    <Button text={t('cards.Transfer')} size="sm"
                                            onClick={() => handleShowTransfer(card.tokenId)}/>
                                </div>
                            </div>
                        </div>)
                    }

                    <Modal
                        show={showRenameFunc}
                        onHide={closeRename}
                        className="rename-box"
                    >
                        <Modal.Header closeButton>
                            <Modal.Title>{t('cards.Rename')}</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            {
                                isRenameSuccess ?
                                    <p className="success-text">{t('cards.Successfully')}️</p> : null
                            }
                            <div className="group-info">
                                <img src={card.imgUrl} alt=""/>
                                <div className="information">
                                    <CardDesc card={card}/>
                                    {renameAmountNoEnough && <p>
                                        {t('cards.BalanceOf', {
                                            name: 'LGD'
                                        })}：{lgdBalance}
                                    </p>}
                                    {renameAmountNoEnough && <p className="errorMsg">
                                        {t('cards.reRandomDescBalanceOutOf', {
                                            name: 'LGD'
                                        })}
                                    </p>}
                                </div>
                            </div>
                            {
                                !isRenameSuccess ?
                                    <div>
                                        <div className="form-group">
                                            <div className="left">
                                                <label>
                                                    {t('cards.EnterName')}
                                                </label>
                                                <div className="group-input">
                                                    <div className={isRenameError ? "bg-red" : "bg-black-name"}>
                                                        <input placeholder={t('cards.msg4')} type="text"
                                                               value={renameValue} className="rename-input"
                                                               disabled={renameAmountNoEnough}
                                                               onChange={(e) => handleChangeName(e)}/>
                                                    </div>
                                                    {
                                                        isRenameError ?
                                                            <span className="error">
                                                            {renameErrorMsg}
                                                          </span> : null
                                                    }

                                                </div>
                                            </div>
                                            <div className="right">
                                                <Button disabled={isRenameError || isLoading || renameAmountNoEnough}
                                                        text={isLoading ? t('common.ConfirmLoading') : t('common.Confirm')}
                                                        onClick={() => {
                                                            handleSubmitRename()
                                                        }} size="sm"/>
                                            </div>
                                        </div>
                                        <p>
                                            {t('cards.NeedConsume', {
                                                count: renameAmount,
                                                name: 'LGD'
                                            })}
                                        </p>
                                    </div> : null
                            }
                        </Modal.Body>
                    </Modal>

                    <Modal
                        show={showTransferFunc}
                        onHide={closeTransfer}
                        className="rename-box"
                    >
                        <Modal.Header closeButton>
                            <Modal.Title>Transfer</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            {
                                isTranferSuccess ?
                                    <p className="success-text">{t('cards.Successfully')}</p> : null
                            }
                            <div className="group-info">
                                <img src={card.imgUrl} alt=""/>
                                <div className="information">
                                    <CardDesc card={card}/>
                                </div>
                            </div>
                            {
                                !isTranferSuccess ?
                                    <div className="form-group">
                                        <div className="left">
                                            <label>{t('cards.Receiver')}</label>
                                            <div className="group-input">
                                                <div className={isTranferError ? "bg-red" : "bg-black-name"}>
                                                    <input placeholder={t('cards.msg2')} type="text"
                                                           className="rename-input"
                                                           value={transferValue}
                                                           onChange={(e) => handleChangeAddress(e)}/>
                                                </div>
                                                {
                                                    isTranferError ?
                                                        <span className="error">
                            {tranferErrorMsg}
                          </span> : null
                                                }

                                            </div>
                                        </div>
                                        <div className="right">
                                            <Button disabled={isTranferError || !transferValue || isLoading}
                                                    text={isLoading ? "Transfer..." : t('cards.Transfer')}
                                                    onClick={() => handleSubmitReceiver()} size="sm"/>
                                        </div>
                                    </div> : null
                            }
                        </Modal.Body>
                    </Modal>
                </div>
            </div>
        </div>
    )
}

export default SearchCard
