import React, {useCallback, useEffect} from 'react'
import styled from 'styled-components'
import brandIcon from '../../../assets/img/logo/brand.png'
import styles from './nav.module.css'
import Button from '../../../components/Button'
import './nav.module.css'
import {Collapse, Container, Nav, Navbar, NavbarToggler, NavItem} from 'reactstrap';
import {NavLink, useLocation} from 'react-router-dom'
import {useTranslation} from "react-i18next";
import {generateLink, useFetchState, useLgdBalance} from "../../../utils";
import {useWallet} from "use-wallet";
import {Modal} from "react-bootstrap";
import {setupNetwork} from "../../../utils/wallet";

const langName: any = {
    en: 'English',
    zh: '中文',
}
const langNameKey: any = {
    en: "zh",
    zh: "en",
}
const NavBar: React.FC = () => {
    const wallet = useWallet()
    const {lgdBalance} = useLgdBalance()
    const account = wallet.account
    const {t, i18n} = useTranslation()
    const {pathname, search} = useLocation()
    const [openLanguage, setOpenLanguage] = useFetchState(false)
    const [showBalance, setShowBalance] = useFetchState(false)
    const [isOpen, setIsOpen] = useFetchState(false);
    const toggle = () => setIsOpen(!isOpen);

    const toggleLang = useCallback((e: any) => {
        if (!e.target.classList.contains('lang-btn')) {
            setOpenLanguage(false)
        }
        if (!e.target.classList.contains('navbar-toggler') && !e.target.closest('.navbar-toggler')) {
            setIsOpen(false)
        }
    }, [setOpenLanguage, setIsOpen])

    useEffect(() => {
        window.document.addEventListener('click', toggleLang, false)
        return () => {
            window.document.removeEventListener('click', toggleLang, false)
            setOpenLanguage(false)
            setIsOpen(false)
        }
    }, [pathname, setOpenLanguage, toggleLang, setIsOpen])

    useEffect(() => {
        if (wallet.status === 'disconnected') {
            wallet.connect('injected').then()
        }
    }, [wallet])

    const changeLanguage = (lng: string) => {
        const oldLanguage = i18n.language;
        if (oldLanguage === lng) {
            return;
        }

        i18n.changeLanguage(lng, function () {
            if (Array.isArray(i18n.options.fallbackLng)) {
                let pathName = pathname === '/' ? '' : pathname
                pathName = pathName.replace(new RegExp(`\\/(${Object.keys(langName).join('|')})`, 'i'), '')
                let url = '/' + lng + pathName

                window.history.pushState({}, '', url)
            }
        })
    };

    return (
        <Navbar expand="lg" className="NavBarContainer">
            <Container className="themed-container" fluid="xl">
                <StyledLink to={{pathname: generateLink(i18n, '/'), search}}>
                    <img src={brandIcon} alt="legend" className="logoImg"/>
                </StyledLink>

                <NavbarToggler onClick={toggle}/>
                <Collapse isOpen={isOpen} navbar>
                    <Nav className="mr-auto nav-bar justify-content-evenly" navbar style={{flex: 1}}>
                        <NavItem>
                            <StyledLink className={styles.navLink}
                                        to={{
                                            pathname: generateLink(i18n, '/cardshop'),
                                            search
                                        }}>{t('menu.card')}</StyledLink>
                        </NavItem>
                        <NavItem>
                            <StyledLink className={styles.navLink} exact
                                        to={{
                                            pathname: generateLink(i18n, '/pool'),
                                            search
                                        }}>{t('menu.pool')}</StyledLink>
                        </NavItem>
                    </Nav>
                    <div className="buttonGroup">
                        <Button
                            account={account}
                            onClick={() => setShowBalance(true)}
                            text={account ? t('menu.MyAccount') : t('menu.UnlockAccount')}
                            size='sm'
                        />
                        <Button
                            account={account}
                            text={t('menu.Heco')}
                            size='sm'
                            bgColor='blue'
                            onClick={setupNetwork}
                        />
                        <LanguageButton
                            className='lang-btn'
                            size='sm'
                            bgColor='blue'
                            onClick={() => {
                                changeLanguage(i18n.language === 'zh' ? 'en' : 'zh')
                                // setOpenLanguage(!openLanguage)
                            }}>
                            {
                                t('common.SwitchLang', {
                                    lang: langName[langNameKey[i18n.language]]
                                })
                            }
                        </LanguageButton>

                        <LanguageList
                            className={openLanguage ? 'showLang' : ''}
                        >
                            {Object.keys(langName).map(item => <LangItem
                                size='sm'
                                bgColor='blue'
                                className={item === i18n.language ? 'active' : ''}
                                key={item}
                                onClick={() => {
                                    changeLanguage(item)
                                }}
                            >
                                {langName[item]}
                            </LangItem>)}
                        </LanguageList>
                    </div>
                </Collapse>

                <Modal show={showBalance} onHide={() => setShowBalance(false)}>
                    <Modal.Header closeButton>
                    </Modal.Header>
                    <Modal.Body>
                        <p>{t('menu.CurrentAccount')} : {account}</p>
                        <p>{t('menu.BalanceOf', {name: 'LGD'})} : {lgdBalance}</p>
                    </Modal.Body>
                </Modal>
            </Container>
        </Navbar>
    )
}
const LanguageList = styled.div`
  position: absolute;
  z-index: 1;
  top: 100%;
  right: 1rem;
  display: none;
  box-sizing: border-box;
  flex-direction: column;

  &.showLang {
    display: flex;
  }
`

const LanguageButton = styled(Button)`
  user-select: none;
  position: relative;
  width: auto;
  padding-left: 1rem;
  padding-right: 1rem;
  min-width: 134px;

    // &:hover ${LanguageList}{
  //   display: flex;
  // }
`

const LangItem = styled(Button)`
  width: 160px;

  &.active,
  &:hover {
    background-color: rgba(0, 0, 0, 0.1);
  }
`

const StyledLink = styled(NavLink)`
  &.active {
    color: #FF9E00;
  }
`
export default NavBar
