import React from 'react'
import styled from 'styled-components'

import {Container} from 'reactstrap'
import Button from '../../../components/Button'
import threeStar from '../../../assets/img/background/three-star.png';
import {useTranslation} from "react-i18next";
import {generateLink} from "../../../utils";
import {useHistory} from "react-router";

const HomePageSection1: React.FC = () => {
    const {i18n, t} = useTranslation()
    const history = useHistory()
    return (
        <Container className="themed-container battle" fluid="xl">
            <Container className="themed-container battle-container" fluid="lg">
                <StyledWrap>
                    <figure>
                        <img className="img-fluid" src={threeStar} alt="three-start"/>
                    </figure>
                    <StyledTitle>{t('home.msg4')}</StyledTitle>

                    <StyledWrapButton>
                        <Button onClick={() => {
                            history.push(generateLink(i18n, '/pool'))
                        }} text={t('home.GetRewards')}/>
                    </StyledWrapButton>
                </StyledWrap>

            </Container>
        </Container>
    )
}

const StyledWrap = styled.div`
  align-items: center;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  padding-bottom: ${(props) => props.theme.spacing[6]}px;
  padding-top: 25px;
  margin: 0 auto;
  width: 66%;
`

const StyledWrapButton = styled.div`
  align-items: center;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  padding-bottom: ${(props) => props.theme.spacing[6]}px;
  padding-top: 78px;
  margin: 0 auto;
  width: 36%;
  justify-content: space-around;
`

const StyledTitle = styled.h1`
  color: ${(props) => props.theme.color.white};
  font-style: normal;
  font-weight: normal;
  font-size: 56px;
  line-height: 100%;
  margin: 0;
  padding: 0;
  text-align: center;
  --stroke-color: #560202;
  --stroke-width: 4px;
  color: white;
  text-shadow: var(--stroke-width) 0 0 var(--stroke-color), calc(var(--stroke-width) * -1) 0 0 var(--stroke-color), 0 var(--stroke-width) 0 var(--stroke-color), 0 calc(var(--stroke-width) * -1) 0 var(--stroke-color);
`

export default HomePageSection1
