import React, {useEffect} from 'react'
import './ListEquip.css'
import EquipItem from './EquipItem'
import {Contract} from "web3-eth-contract";
import {useWallet} from "use-wallet";
import {getAvatarUrl, useFetchState} from "../../../utils";
import {Modal} from "react-bootstrap";
import FilterCard from "../../Cards/components/FilterCard";
import CardItemConfirm from "../../Cards/components/CardItemConfirm";
import Button from "../../../components/Button";
import {heroArmory, heroPool, lgdHero} from "../../../sushi/utils";
import {CardQuality} from "../../../utils/card-quality";
import styled from "styled-components";
import {useTranslation} from "react-i18next";

interface ListEquipProps {
    heroPoolContract: Contract
    heroContract: Contract
    lgdHeroContract: Contract
    web3: any
    stakedCards: any[]
    changeStakedCards: () => void
}

const ListEquip: React.FC<ListEquipProps> = (
    {
        heroPoolContract,
        heroContract,
        lgdHeroContract,
        web3,
        stakedCards,
        changeStakedCards
    }
) => {
    const {account} = useWallet()
    const {t} = useTranslation()
    const pageSize = window.innerWidth < 768 ? 1 : 5
    const [list, setList] = useFetchState([])
    const [removingList, setRemovingList] = useFetchState([])
    const [myCard, setMyCard] = useFetchState({})
    const [myCardRows, setMyCardRows] = useFetchState([])
    const [fullMyCardRows, setFullMyCardRows] = useFetchState([])
    const [searchValue, setSearchValue] = useFetchState('')
    const [showApprove, setShowApprove] = useFetchState(false)
    const [showListSelectCard, setShowListSelectCard] = useFetchState(false)
    const [allowanceLoading, setAllowanceLoading] = useFetchState(false)
    const [allowance, setAllowance] = useFetchState(false)

    const handleClickConfirm = (e: any, index: number) => {
        const myCard = myCardRows[index]
        setMyCard(myCard)
    }
    const submitMyCard = (card: any) => {
        if (heroPoolContract && myCard?.tokenId && account) {
            let list = myCardRows.map((item: any) => {
                if (item.tokenId === card.tokenId) {
                    item.confirming = true
                }
                return item
            })
            setMyCardRows(list)
            setFullMyCardRows(list)

            heroPool.stake(heroPoolContract, myCard.tokenId, account)
                .then(res => {
                    setShowListSelectCard(false)
                    setMyCardRows([])
                    setFullMyCardRows([])
                    changeStakedCards()
                    setMyCard({})
                })
                .catch(() => {
                    setShowListSelectCard(false)
                    setMyCardRows([])
                    setFullMyCardRows([])
                    changeStakedCards()
                    setMyCard({})
                })
        }
    }

    function onKeydownSearch(e: any) {
        if (e.keyCode === 13) {
            search()
        }
    }

    function search() {
        let list = [...fullMyCardRows].filter(item => {
            if (searchValue === '') {
                return true
            }
            try {
                return new RegExp(searchValue, 'i').test(item.name)
            } catch (e) {
                return item.name.includes(searchValue)
            }
        })
        setMyCardRows(list)
    }

    const getApprove = () => {
        setAllowanceLoading(true)
        lgdHero.approvalForAll(lgdHeroContract, heroPoolContract.options.address, account).then(res => {
            setAllowance(res)
            setAllowanceLoading(false)
        }).catch(() => {
            setAllowanceLoading(false)
        })
    }
    const removeCard = (item: any) => {
        if (removingList.includes(item.tokenId)) {
            return
        }
        let newRemovingList = [
            ...removingList
        ]
        newRemovingList.push(item.tokenId)
        setRemovingList(newRemovingList)

        heroPool.withdraw(heroPoolContract, item.tokenId, account).then(() => {
            changeStakedCards()
            let newList = list.filter((card: any) => card.tokenId !== item.tokenId)
            if (newList.lengh < 5) {
                newList.push({
                    key: Math.random().toString()
                })
            }
            setList(newList)
            setRemovingList(removingList.filter((id: string) => id !== item.tokenId))
        }).catch(e => {
            setRemovingList(removingList.filter((id: string) => id !== item.tokenId))
        })
    }
    useEffect(() => {
        if (heroPoolContract && lgdHeroContract && account) {
            setAllowanceLoading(true)
            lgdHero.isApprovedForAll(lgdHeroContract, heroPoolContract.options.address, account).then(isApproved => {
                setAllowance(isApproved)
                setAllowanceLoading(false)
            }).catch(() => {
                setAllowanceLoading(false)
            })
        }
    }, [heroPoolContract, lgdHeroContract, account, setAllowance, setAllowanceLoading])

    useEffect(() => {
        let newList = [...stakedCards]
        let placeholder = []
        if (stakedCards.length < pageSize) {
            for (let i = 0; i < pageSize - stakedCards.length; i++) {
                placeholder.push({
                    key: Math.random().toString()
                })
            }
        } else {
            let index = newList.findIndex(item => item.key !== undefined)
            if (index < 0) {
                placeholder.push({
                    key: Math.random().toString()
                })
            }
        }
        console.log('setList newList')
        setList(newList.concat(placeholder))
    }, [stakedCards, setList, pageSize])

    useEffect(() => {
        if (heroContract && showListSelectCard) {
            heroArmory.getHeros(heroContract, account).then(res => {
                const all = res.map((item: any) => {
                    let q = CardQuality[item.quality]
                    let card = myCardRows.filter((card: any) => card.tokenId === item.tokenId)?.[0] || {}

                    return {
                        name: item.name,
                        quality: q.name,
                        fightRate: q.fight,
                        cardShowRate: q.rate,
                        fight: item.fight,
                        prop: item.prop,
                        tokenId: item.tokenId,
                        imgUrl: getAvatarUrl(item),
                        confirming: card.confirming
                    }
                })

                setMyCardRows(all)
                setFullMyCardRows(all)
            })
        }
    }, [myCardRows, heroContract, web3, account, showListSelectCard, stakedCards, setFullMyCardRows, setMyCardRows])

    return (
        <div className="list__equip">
            <h2>{t('pool.HasEquipedCards', {count: stakedCards.length})}</h2>
            <div className="list__equip--item">
                {
                    list.map((card: any) => <EquipItem
                        onClick={() => allowance ? (card.tokenId ? removeCard(card) : setShowListSelectCard(true)) : setShowApprove(true)}
                        key={(card.tokenId || card.key)}
                        card={{
                            ...card,
                            removing: removingList.includes(card.tokenId)
                        }}
                    />)
                }
            </div>
            <div className="list-select-card">
                <Modal
                    show={showListSelectCard}
                    onHide={() => setShowListSelectCard(false)}
                    className="list-card-modal list-select-card "
                >
                    <Modal.Header closeButton>
                        <Modal.Title>{t('pool.SelectCard')}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <FilterCard
                            onChange={(e: any) => {
                                setSearchValue(e.target.value)
                            }}
                            onKeyDown={(e: any) => {
                                onKeydownSearch(e)
                            }}
                            onClick={search}
                            value={searchValue}
                        />
                        <div className="list">
                            {myCardRows.map((card: any, index: number) => <CardItemConfirm
                                key={index}
                                activeClass={card.tokenId === myCard.tokenId ? 'active' : ''}
                                handleClickConfirm={(e: any) => handleClickConfirm(e, index)}
                                card={card}
                            />)}
                        </div>
                        <div className="confirm-btn">
                            <Button text={myCard.confirming ? t('common.ConfirmLoading') : t('common.Confirm')}
                                    size="sm"
                                    disabled={myCardRows.length === 0 || myCard.confirming}
                                    onClick={() => submitMyCard(myCard)}/>
                        </div>
                    </Modal.Body>
                </Modal>
                <Modal
                    show={showApprove && !allowance}
                    onHide={() => {
                        setShowApprove(false)
                        setShowListSelectCard(true)
                    }}
                >
                    <Modal.Header closeButton>
                        <Modal.Title>{t('common.Approve')}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <ApproveBox>
                            <Button text={allowanceLoading ? t('cards.approveLoading') : t('common.Approve')} size="sm"
                                    disabled={allowanceLoading}
                                    onClick={getApprove}/>
                        </ApproveBox>
                    </Modal.Body>
                </Modal>
            </div>
        </div>
    )
}

const ApproveBox = styled.div`
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
`
export default ListEquip
