import React, {useEffect} from 'react'
import styled from 'styled-components'
import {useWallet} from 'use-wallet'

import metamaskLogo from '../../assets/img/metamask-fox.svg'
import walletConnectLogo from '../../assets/img/wallet-connect.svg'

import Button from '../Button'
import Modal, {ModalProps} from '../Modal'
import ModalActions from '../ModalActions'
import ModalContent from '../ModalContent'
import ModalTitle from '../ModalTitle'
import Spacer from '../Spacer'

import WalletCard from './components/WalletCard'
import './WalletProviderModal.css'


const WalletProviderModal: React.FC<ModalProps> = ({onDismiss}) => {
    const {account, connect} = useWallet()

    useEffect(() => {
        if (account) {
            onDismiss()
        }
    }, [account, onDismiss])

    return (
        <Modal>
            <ModalTitle text="Select a wallet provider."/>

            <ModalContent>
                <StyledWalletsWrapper>
                    <StyledWalletCard className="wallet-modal-card-item">
                        <WalletCard
                            icon={<img src={metamaskLogo} style={{height: 32}} alt=""/>}
                            onConnect={() => {
                                console.log('connect wallet click....')
                                connect('injected')
                            }}
                            title="Metamask"
                        />
                    </StyledWalletCard>
                    <StyledWalletCard className="wallet-modal-card-item">
                        <WalletCard
                            icon={<img src={walletConnectLogo} style={{height: 24}} alt=""/>}
                            onConnect={() => connect('walletconnect')}
                            title="WalletConnect"
                        />
                    </StyledWalletCard>
                </StyledWalletsWrapper>
            </ModalContent>

            <ModalActions>
                <Button text="Cancel" variant="secondary" onClick={onDismiss}/>
            </ModalActions>
        </Modal>
    )
}

const StyledWalletsWrapper = styled.div`
  display: flex;
  @media (max-width: ${(props) => props.theme.breakpoints.mobile}px) {
    flex-direction: column;
    flex-wrap: unset;
  }
`

const StyledWalletCard = styled.div`
  flex: 1;
  flex-basis: calc(50% - ${(props) => props.theme.spacing[2]}px);
  &:last-child{
    margin-left: 10px;
  }
`

export default WalletProviderModal
