import BigNumber from 'bignumber.js'
import {useCallback, useEffect, useRef, useState} from 'react';
import useSushi from "../hooks/useSushi";
import {useWallet} from "use-wallet";
import {balanceOf} from "./erc20";
import useBlock from "../hooks/useBlock";

export {default as formatAddress} from './formatAddress'

export const bnToDec = (bn: BigNumber, decimals = 18): number => {
    return bn.dividedBy(new BigNumber(10).pow(decimals)).toNumber()
}

export const decToBn = (dec: number, decimals = 18) => {
    return new BigNumber(dec).multipliedBy(new BigNumber(10).pow(decimals))
}

export const getAvatarUrl = (card: any) => {
    return `https://legend-shot.github.io/avatar/avatar_${card.quality}_${card.heroTypeId}_${card.prop}.png`
}


export const generateLink = (i18n: any, link: string) => {
    let baseUrl = `/${i18n.language}`;
    if (link === '/') {
        return `${baseUrl}`;
    }

    if (baseUrl === "/") {
        baseUrl = "";
    }

    return link.startsWith('/') ? `${baseUrl}${link}` : `${baseUrl}/${link}`;
}

export function useFetchState(props?: any) {
    const focus: any = useRef();
    const [stateVal, setStateVal] = useState(props);
    useEffect(() => {
        focus.current = true;
    }, []);
    const setFetchState = useCallback((params) => {
        focus.current && setStateVal(params);
        return () => (focus.current = false);
    }, []);
    return [stateVal, setFetchState];
}

export function useLgdBalance() {
    const sushi: any = useSushi()
    const {account} = useWallet()
    const block = useBlock()
    const [lgdBalance, setLgdBalance] = useFetchState()
    const [bigLgdBalance, setBigLgdBalance] = useFetchState(new BigNumber(0))
    useEffect(() => {
        if (sushi && account) {
            balanceOf(sushi.contracts.lgdToken, account).then(res => {
                setLgdBalance(sushi.web3.utils.fromWei(res))
                setBigLgdBalance(new BigNumber(res))
            })
        }
    }, [sushi, account, block, setBigLgdBalance, setLgdBalance])

    return {
        lgdBalance,
        bigLgdBalance
    }
}

export async function getFile(url: string) {
    return fetch(url, {
        method: 'get'
    }).then((res: any) => {
        if (res.status === 200) {
            return res.blob();
        }
        return url
    }).then(blob => {
        if (/^http/.test(blob)) {
            return {
                blob: null,
                url: blob
            }
        }

        let bl = new Blob([blob], {type: "image/png"});
        let url = blobToUrl(bl);
        return {
            blob: blob,
            url: url
        }
        // window.URL.revokeObjectURL(link.href);
    })
}

export function blobToUrl(blob: any) {
    return window.URL.createObjectURL(blob)
}

export function base64ToBlob(base64Data: string) {
    //console.log(base64Data);//data:image/png;base64,
    var byteString;
    if (base64Data.split(',')[0].indexOf('base64') >= 0)
        byteString = atob(base64Data.split(',')[1]);//base64 解码
    else {
        byteString = unescape(base64Data.split(',')[1]);
    }
    var mimeString = base64Data.split(',')[0].split(':')[1].split(';')[0];//mime类型 -- image/png

    // var arrayBuffer = new ArrayBuffer(byteString.length); //创建缓冲数组
    // var ia = new Uint8Array(arrayBuffer);//创建视图
    var ia = new Uint8Array(byteString.length);//创建视图
    for (var i = 0; i < byteString.length; i++) {
        ia[i] = byteString.charCodeAt(i);
    }
    var blob = new Blob([ia], {
        type: mimeString
    });
    return blob;
}

export async function blobToBase64(blob: any) {
    return new Promise(resolve => {
        var reader = new FileReader();
        reader.readAsDataURL(blob)
        reader.onload = (e) => {
            resolve(e.target.result)
        }
    })
}