import React, {useCallback, useEffect} from 'react'
import Button from '../../../components/Button'
import {Modal} from 'react-bootstrap'
import carousel1 from '../../../assets/img/card/card-1.png'
import arrowleft from '../../../assets/img/arrow-left.png'
import arrowright from '../../../assets/img/arrow-right.png'
import fire from '../../../assets/img/fire.png'
import Slider from 'react-slick'
import './Carousels.css'
import FilterCard from './FilterCard'
import CardItemConfirm from './CardItemConfirm'
import fireball from '../../../assets/img/fireball.png'
import {useWallet} from "use-wallet";
import {approve, heroArmory, isZeroAddress, lgdHero, shop} from '../../../sushi/utils'
import {Contract} from "web3-eth-contract";
import {getAllowance, getContract} from "../../../utils/erc20";
import {provider} from "web3-core";
import BigNumber from "bignumber.js";
import {
    base64ToBlob,
    blobToBase64, blobToUrl,
    generateLink,
    getAvatarUrl,
    getFile,
    useFetchState,
    useLgdBalance
} from "../../../utils";
import {useTranslation} from "react-i18next";
import {useHistory} from "react-router";
import {CardQuality} from "../../../utils/card-quality";
import CardDesc from '../../../components/CardDesc'

interface CarouselsProps {
    shopContract: Contract
    heroContract: Contract
    lgdHeroContract: Contract
    lgdTokenContract: Contract
    web3: any
}

const defaultCard: any = {
    name: '',
    quality: '',
    fightRate: '',
    cardShowRate: '',
    prop: '',
    fight: ''
}
let defaultSettings: any = {
    dots: false,
    infinite: false, // loop
    speed: 300,
    slidesToShow: /mobile/i.test(navigator.userAgent) ? 1 : 3,
    slidesToScroll: 1,
    initialSlide: 0,
    nav: true,
}

const Carousels: React.FC<CarouselsProps> = (
    {
        shopContract,
        heroContract,
        lgdHeroContract,
        lgdTokenContract,
        web3
    }
) => {
    let sliderRef: any = null
    const {i18n, t} = useTranslation()
    const history = useHistory()
    const {lgdBalance} = useLgdBalance()
    const [spinnerCard, setSpinnerCard] = useFetchState('');
    const [settings, setSettings] = useFetchState(defaultSettings);
    const {account, ethereum}: { account: string, ethereum: any } = useWallet()
    const [allowanceLoading, setAllowanceLoading] = useFetchState(false)
    const [burnApproveBtnName, setBurnApproveBtnName] = useFetchState('')
    const [burnApproveLoading, setBurnApproveLoading] = useFetchState(false)
    const [requestedApproval, setRequestedApproval] = useFetchState(false)
    const [rows, setRows] = useFetchState([])
    const [myCard, setMyCard] = useFetchState(defaultCard)
    const [myCardRows, setMyCardRows] = useFetchState([])
    const [fullMyCardRows, setFullMyCardRows] = useFetchState([])
    const [searchValue, setSearchValue] = useFetchState('')
    const [heroAmount, setHeroAmount] = useFetchState('--')
    const [lgdNoEnough, setLgdNoEnough] = useFetchState(false)
    const [giveAmount, setGiveAmount] = useFetchState('')
    const [cardIndex, setCardIndex] = useFetchState(-1)
    const [show, setShow] = useFetchState(false)
    const [showBurn, setShowBurn] = useFetchState(false);
    const [showSuccess, setShowSuccess] = useFetchState(false)
    const [randomImgTag, setRandomImgTag] = useFetchState('')
    const [byCard, setByCard] = useFetchState(defaultCard)

    const [showConfirmSuccess, setShowConfirmSuccess] = useFetchState(false)
    const [showListSelectCard, setShowListSelectCard] = useFetchState(false)
    const [showLoadingBurn, setShowLoadingBurn] = useFetchState(false)
    const [showLoadingBurnHero, setShowLoadingBurnHero] = useFetchState(false)

    const handleClose = () => setShow(false)

    const handleCloseShowSuccess = () => setShowSuccess(false)

    const handleCloseConfirmSuccess = () => setShowConfirmSuccess(false)

    const closeSelectCard = () => setShowListSelectCard(false)


    function SampleNextArrow(props: any) {
        const {className, style, onClick} = props
        return (
            <div className={className} style={{...style, display: 'block'}}>
                <img src={arrowright} alt="" onClick={onClick}/>
            </div>
        )
    }

    function SamplePrevArrow(props: any) {
        const {className, style, onClick} = props
        return (
            <div className={className} style={{...style, display: 'block'}}>
                <img src={arrowleft} alt="" onClick={onClick}/>
            </div>
        )
    }

    const showSelectCard = useCallback(async () => {
        if (lgdHeroContract && shopContract && account && ethereum) {
            setBurnApproveLoading(true)
            let allowance721: boolean = false
            let allowanceErc20: boolean = false
            setBurnApproveBtnName(t('cards.approveLoading'))
            allowance721 = await lgdHero.isApprovedForAll(lgdHeroContract, shopContract.options.address, account)
            if (!allowance721) {
                const hash721 = await lgdHero.approvalForAll(lgdHeroContract, shopContract.options.address, account)
                allowance721 = !!hash721
            }
            if (allowance721) {
                const alloErc20 = await getAllowance(lgdTokenContract, shopContract, account)
                allowanceErc20 = new BigNumber(alloErc20).toNumber() !== 0
                if (!allowanceErc20) {
                    const hashErc20 = await approve(lgdTokenContract, shopContract, account)
                    allowanceErc20 = !!hashErc20
                }
            }
            setBurnApproveBtnName('')

            setShowListSelectCard(allowance721 && allowanceErc20)
            setBurnApproveLoading(false)
        }
    }, [lgdHeroContract, shopContract, ethereum, account, lgdTokenContract, setBurnApproveBtnName, setBurnApproveLoading, setShowListSelectCard, t])

    const closeLoadingBurn = () => setShowLoadingBurn(false)

    const closeLoadingBurnHero = () => setShowLoadingBurnHero(false)

    const handleClickConfirm = (e: any, index: number) => {
        const myCard = {...myCardRows[index]}
        setMyCard(myCard)
    }

    const setAllowance = useCallback((card: any, bool: boolean) => {
        if (rows.length) {
            let needUpdate = false
            const list = rows.map((item: any) => {
                if (card.tokenId === item.tokenId && item.allowance !== bool) {
                    item.allowance = bool
                    needUpdate = true
                }
                return item
            })
            if (needUpdate) {
                console.log('setAllowance')
                setRows(list)
            }
        }
    }, [rows, setRows])

    const openBurnDialog = useCallback(async () => {
        if (!lgdBalance) {
            return
        }
        if (shopContract && myCard.tokenId && account) {
            setShowBurn(false)
            setShowListSelectCard(false)
            setShowLoadingBurn(true)
            shop.reRandomHero(shopContract, myCard.tokenId, account)
                .then(res => {
                    const item = res?.events?.Buy?.returnValues?.hero
                    let q = CardQuality[item.quality]

                    const cardBack: any = {
                        name: item.name,
                        quality: q.name,
                        fightRate: q.fight,
                        cardShowRate: q.rate,
                        fight: item.fight,
                        prop: item.prop,
                        tokenId: item.tokenId,
                        imgUrl: getAvatarUrl(item)
                    }
                    setMyCard(cardBack)
                    setShowConfirmSuccess(true)
                    setShowLoadingBurn(false)
                    setShowListSelectCard(false)
                }).catch(() => {
                setShowLoadingBurn(false)
                setShowListSelectCard(false)
            })
        }
    }, [lgdBalance, shopContract, myCard, account, setMyCard, setShowBurn, setShowConfirmSuccess, setShowListSelectCard, setShowLoadingBurn])
    const handleApprove = useCallback(async () => {
        let card = {...rows[cardIndex]}
        if (card && Object.keys(card).length) {
            console.log('handleApprove')
            setRequestedApproval(true)
            const contract = getContract(ethereum as provider, card.payToken)
            approve(contract, shopContract, account).then(txHash => {
                if (txHash) {
                    setAllowance(card, true)
                }
                setRequestedApproval(false)
            }).catch(() => {
                setRequestedApproval(false)
            })
        }
    }, [cardIndex, setRequestedApproval, shopContract, account, ethereum, rows, setAllowance])

    const openBuyDialog = () => {
        let card = {...rows[cardIndex]}
        if (card && Object.keys(card).length && shopContract && ethereum && account) {
            setShow(true)
            shop.buy(shopContract, cardIndex, card.payToken, card.price, account).then(res => {
                const giveLGD = web3.utils.fromWei(res.events.Buy.returnValues.giveAmount)
                const item = res?.events?.Buy?.returnValues?.hero
                let q = CardQuality[item.quality]

                const cardBack: any = {
                    name: item.name,
                    quality: q.name,
                    fightRate: q.fight,
                    cardShowRate: q.rate,
                    fight: item.fight,
                    prop: item.prop,
                    tokenId: item.tokenId,
                    imgUrl: getAvatarUrl(item),
                }
                setGiveAmount(giveLGD)
                setShowSuccess(true)
                setByCard(cardBack)
                setShow(false)
            }).catch(e => {
                setShow(false)
            })
        }
    }

    function handleClickItem(e: any, index: number) {
        setCardIndex(index)
    }

    const buyButtonText = useCallback(() => {
        let card = {...rows[cardIndex]}
        if (!card && Object.keys(card).length) {
            return t('common.Buy')
        }
        if (card.status === '1' && !card.allowance) {
            return t('common.Approve')
        } else {
            return card.statusName
        }
    }, [cardIndex, rows, t])

    function onKeydownSearch(e: any) {
        if (e.keyCode === 13) {
            search()
        }
    }

    function search() {
        let list = [...fullMyCardRows].filter(item => {
            if (searchValue === '') {
                return true
            }
            try {
                return new RegExp(searchValue, 'i').test(item.name)
            } catch (e) {
                return item.name.includes(searchValue)
            }
        })
        setMyCardRows(list)
    }

    useEffect(() => {
        let card = {...rows[cardIndex]}
        const getAl = async () => {
            setAllowanceLoading(true)
            const contract = getContract(ethereum as provider, card.payToken)
            const allo = await getAllowance(contract, shopContract, account)
            setAllowance(card, new BigNumber(allo).toNumber() !== 0)
            setAllowanceLoading(false)
        }

        if (card && Object.keys(card).length) {
            let is = isZeroAddress(card.payToken)
            console.log('get allowance', is)
            if (card.status !== '1' || is) {
                setAllowance(card, true)
            } else {
                if (!is) {
                    getAl().then()
                }
            }
        }
    }, [cardIndex, shopContract, ethereum, account, rows, setAllowanceLoading, setAllowance])

    const sliderGoTo = useCallback((index: number) => {
        if (index > -1) {
            defaultSettings.initialSlide = index
            setSettings(defaultSettings)
            setCardIndex(index)
            if (index > 1) {
                index--
                sliderRef?.slickGoTo(index)
            }
        }
    }, [defaultSettings, setSettings, setCardIndex, sliderRef])
    useEffect(() => {
        if (shopContract) {
            console.log('sliderGoTo')
            shop.getAllBlindBox(shopContract, account).then(res => {
                let statusName: any = {
                    0: t('common.Comingsoon'),
                    1: t('common.Buy'),
                    2: t('common.Locked'),
                    3: t('common.Soldout')
                }

                let canBuyItemIndex = -1
                const list = res
                    .map((item: any, index: number) => {
                        if (item.status === '1') {
                            canBuyItemIndex = index
                        }
                        return {
                            statusName: statusName[item.status],
                            status: item.status,
                            supply: item.supply,
                            remaining: item.remaining,
                            price: web3.utils.fromWei(item.price),
                            payToken: item.payToken,
                            // symbol: 'LGD'
                        }
                    })
                    .filter((card: any) => card.status !== '4')
                    .map((card: any) => {
                        if (isZeroAddress(card.payToken)) {
                            return {
                                ...card,
                                symbol: 'HT',
                            }
                        } else {
                            const contract = getContract(ethereum as provider, card.payToken)
                            return contract.methods.symbol().call({from: account}).then((res: any) => {
                                return {
                                    ...card,
                                    symbol: res
                                }
                            })
                        }
                    })
                Promise.all(list).then(list => {
                    console.log('cardshow', list)
                    setRows(list)
                    sliderGoTo(canBuyItemIndex)
                })
            })
        }
    }, [shopContract, web3, setRows, t, account, setCardIndex, setSettings, sliderRef, ethereum])
    useEffect(() => {
        if (heroContract && myCardRows.length === 0 && showListSelectCard) {
            heroArmory.getHeros(heroContract, account).then((res) => {
                const all = res.map((item: any) => {
                    let q = CardQuality[item.quality]
                    return {
                        name: item.name,
                        quality: q.name,
                        fightRate: q.fight,
                        cardShowRate: q.rate,
                        fight: item.fight,
                        prop: item.prop,
                        tokenId: item.tokenId,
                        imgUrl: getAvatarUrl(item),
                    }
                })
                setMyCardRows(all)
                setFullMyCardRows(all)
            })
        }
    }, [heroContract, web3, account, myCardRows.length, showListSelectCard, setFullMyCardRows, setMyCardRows])
    useEffect(() => {
        if (shopContract && account) {
            shop.heroAmount(shopContract, account).then(amount => {
                setHeroAmount(web3.utils.fromWei(amount))
                setLgdNoEnough(lgdBalance * 1 < heroAmount * 1)
            })
        }
    }, [shopContract, account, heroAmount, lgdBalance, setHeroAmount, setLgdNoEnough, web3.utils])

    const getImg = useCallback(async (url: string) => {
        return new Promise((resolve, reject) => {
            let oldFile = JSON.parse(localStorage.getItem('randomImg') || '{}')
            let oldUrl = oldFile[url]
            if (oldUrl) {
                if (!/^http/i.test(oldUrl)){
                    oldUrl = blobToUrl(base64ToBlob(oldUrl))
                }
                resolve(oldUrl)
            } else {
                return getFile(url).then(res => {
                    if (res.blob) {
                        blobToBase64(res.blob).then(base => {
                            oldFile[url] = base
                            localStorage.setItem('randomImg', JSON.stringify(oldFile))
                            resolve(res.url)
                        })
                    } else {
                        oldFile[url] = url
                        localStorage.setItem('randomImg', JSON.stringify(oldFile))
                        resolve(res.url)
                    }
                }).catch(reject)
            }
        })

    }, [])
    useEffect(() => {
        if (show && !showSuccess) {
            let url = getAvatarUrl({
                quality: 0,
                heroTypeId: Math.floor(Math.random() * 16),
                prop: 0,
            })
            getImg(url).then(blobUrl => {
                setSpinnerCard(blobUrl)
            }).finally(() => {
                setTimeout(() => {
                    setRandomImgTag(Math.random().toString() + new Date().getTime())
                }, 500)
            })
        }
    }, [show, showSuccess, setSpinnerCard, randomImgTag, getImg, setRandomImgTag])
    return (
        <div className="card-shop">
            <div className="card__carousels">
                <div className="carousels move-up">
                    <div className="text-center">
                        <div className="container-carousels">
                            <Slider
                                ref={slider => {
                                    if (slider) {
                                        sliderRef = slider
                                    }
                                }}
                                {...settings} afterChange={index => sliderGoTo(index)} nextArrow={<SampleNextArrow/>}
                                prevArrow={<SamplePrevArrow/>}
                                className="test">
                                {rows.map((item: any, index: number) =>
                                    <div
                                        key={item.payToken}
                                        className={"item card-item " + (index === cardIndex && 'active') + ' status-' + item.status}
                                        onClick={(e) => handleClickItem(e, index)}
                                    >
                                        <span>{t('cards.Left')}: {item.remaining}</span>
                                        <p>{t('cards.SliderItemDesc')}</p>
                                        <img src={carousel1} alt=""/>
                                        <h2>{item.price} {item.symbol}</h2>
                                    </div>
                                )}
                            </Slider>
                        </div>
                        <div className="btn-buy">
                            {
                                (allowanceLoading || requestedApproval) ? <Button
                                        disabled={true}
                                        text={t('common.Loading')}
                                        size="md"
                                        bgColor="blue"
                                    /> :
                                    <Button
                                        disabled={
                                            !rows[cardIndex]
                                            || rows[cardIndex].status !== '1'
                                        }
                                        text={buyButtonText()}
                                        size="md"
                                        bgColor="blue"
                                        onClick={rows[cardIndex]?.allowance ? openBuyDialog : handleApprove}
                                    />
                            }

                        </div>
                    </div>
                </div>
            </div>
            <div className="card__carousels custom-bottom">
                <div className="carousels">
                    <div className="text-center">
                        <div className="burn">
                            <img className="fire" src={fire} alt=""/>
                            <div>
                                <p>{
                                    lgdNoEnough ?
                                        t('cards.reRandomDescBalanceOutOf', {name: "LGD"})
                                        : t('cards.reRandomDesc', {
                                            name: "LGD",
                                            count: heroAmount
                                        })
                                }</p>
                                <Button disabled={burnApproveLoading || lgdNoEnough}
                                        text={burnApproveLoading ? burnApproveBtnName : t('cards.Burn')}
                                        size="md" onClick={showSelectCard}/>
                            </div>
                        </div>
                        {/* spinner */}
                        <div className="spinner">
                            <Modal show={show} onHide={handleClose} className="spinner-buy">
                                <Modal.Header closeButton>
                                </Modal.Header>
                                <Modal.Body>

                                    <div className="pending">
                                        <figure className="wrap">
                                            <img className="img-fluid buy-only" src={spinnerCard} alt=""/>
                                        </figure>
                                    </div>
                                </Modal.Body>
                            </Modal>
                        </div>
                        {' '}
                        {/* end spinner */}
                        {/* burn */}
                        <div className="spinner">
                            <Modal
                                show={showBurn}
                                className="spinner-buy burning"
                                backdrop="static"
                                keyboard={false}
                            >
                                <Modal.Body>
                                    <div className="pending">
                                        <figure className="wrap">
                                            <img className="img-fluid" src={spinnerCard} alt=""/>
                                        </figure>
                                        <div className="group">
                                            <div className="group-fire">
                                                <figure className="move-down"><img className="fire-ball" src={fireball}
                                                                                   alt=""/></figure>
                                                <figure className="move-up"><img className="fire-ball" src={fireball}
                                                                                 alt=""/></figure>
                                            </div>
                                        </div>
                                    </div>
                                </Modal.Body>
                            </Modal>
                        </div>
                        {' '}
                        {/* end burn */}
                        <div>
                            <Modal
                                show={showSuccess}
                                onHide={handleCloseShowSuccess}
                                className="burn-success"
                            >
                                <Modal.Header closeButton>
                                    <h2 className="title-burn-success">
                                        {t('cards.CONGRATULATION_ON_GETTING', {count: giveAmount, name: 'LGD'})}
                                    </h2>
                                </Modal.Header>
                                <Modal.Body>
                                    <div className="group-info">
                                        <img src={byCard.imgUrl} alt=""/>
                                        <div className="information">
                                            <CardDesc card={byCard}/>
                                        </div>
                                    </div>
                                    <Button text={t('common.ViewCard')} onClick={() => {
                                        handleCloseShowSuccess()
                                        history.push(generateLink(i18n, '/cardshop/mycard'))
                                    }} size="md"/>
                                </Modal.Body>
                            </Modal>
                        </div>
                        {/* end spinner */}
                        <div>
                            <Modal
                                show={showConfirmSuccess}
                                onHide={handleCloseConfirmSuccess}
                                className="burn-success show-confirm-card"
                            >
                                <Modal.Header closeButton>
                                    <h2 className="title-burn-success">
                                        {t('cards.msg5')}
                                    </h2>
                                </Modal.Header>
                                <Modal.Body>
                                    <div className="group-info">
                                        <img src={myCard.imgUrl} alt=""/>
                                        <div className="information">
                                            <CardDesc card={myCard}/>
                                        </div>
                                    </div>
                                </Modal.Body>
                                <Button text={t('common.ViewCard')} size="md" onClick={() => {
                                    history.push(generateLink(i18n, '/cardshop/mycard'))
                                }}/>
                            </Modal>
                        </div>
                        <div className="list-select-card">
                            <Modal
                                show={showListSelectCard}
                                onHide={closeSelectCard}
                                className="list-card-modal list-select-card "
                            >
                                <Modal.Header closeButton>
                                    <Modal.Title>{t('cards.SelectCard')}</Modal.Title>
                                </Modal.Header>
                                <Modal.Body>
                                    <FilterCard
                                        onChange={(e: any) => {
                                            setSearchValue(e.target.value)
                                        }}
                                        onKeyDown={(e: any) => {
                                            onKeydownSearch(e)
                                        }}
                                        onClick={search}
                                        value={searchValue}
                                    />
                                    <div className="list">
                                        {myCardRows.map((card: any, index: number) => <CardItemConfirm
                                            key={index}
                                            activeClass={card.tokenId === myCard.tokenId ? 'active' : ''}
                                            handleClickConfirm={(e: any) => handleClickConfirm(e, index)}
                                            card={card}
                                        />)}
                                    </div>
                                    <div className="confirm-btn">
                                        <Button text={t('common.Confirm')} size="sm"
                                                disabled={myCardRows.length === 0 || showLoadingBurn}
                                                onClick={openBurnDialog}/>
                                    </div>
                                </Modal.Body>
                            </Modal>
                        </div>
                        <div className="loading-burn">
                            <Modal
                                show={showLoadingBurn}
                                onHide={closeLoadingBurn}
                                className="loading-burn"
                            >
                                <Modal.Body>
                                    <div className="group-info">
                                        <img src={carousel1} alt=""/>
                                    </div>
                                </Modal.Body>
                            </Modal>
                        </div>
                        <div className="loading-burn-hero">
                            <Modal
                                show={showLoadingBurnHero}
                                onHide={closeLoadingBurnHero}
                            >
                                <Modal.Body>
                                    <div className="group-info">
                                        <img src={carousel1} alt=""/>
                                    </div>
                                </Modal.Body>
                            </Modal>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Carousels
